import React, { useEffect, Component } from "react";
import { Route, Switch, Redirect, withRouter } from "react-router-dom";
import HomePage from "./pages/Home/Home";
import LoginPage from "./pages/Login/Login";
import OnboardingPage from "./pages/Onboarding/Onboarding";
import "./App.css";
import "./styles/index.scss";
import appRoutes from "./configs/routes";
import "antd/dist/antd.css";
import { connect } from "react-redux";

import { retryLogin } from "./store/actions";

const App = (props) => {
  useEffect(() => {
    console.log("retry mecahnism");
    props.retryLogin();
  }, [props.isAuthenticated]);

  const PrivateRoute = ({ component: Component, ...rest }) => (
    <Route
      {...rest}
      render={(props2) =>
        props.isAuthenticated === true ? (
          <Component {...props2} />
        ) : (
          <Redirect to={appRoutes.homePage} />
        )
      }
    />
  );

  let routes = (
    <Switch>
      <Route path={appRoutes.loginPage} exact component={LoginPage} />
      <Redirect path="*" to={appRoutes.loginPage} />
    </Switch>
  );

  if (props.isAuthenticated) {
    routes = (
      <Switch>
        <Route path={"/home"} component={HomePage} />
        <Route path={"/onboarding"} component={OnboardingPage} />
        <Redirect path="*" to={"/home"} />
      </Switch>
    );
  }

  // return <React.Fragment>{routes}</React.Fragment>;
  // return (
  //   <Switch>
  //     <Route path={appRoutes.loginPage} exact component={LoginPage} />
  //     <PrivateRoute path={"/home"} component={HomePage} />
  //     <PrivateRoute path={"/onboarding"} component={OnboardingPage} />
  //   </Switch>
  // );
  return (
    <Switch>
      <Route path={appRoutes.loginPage} exact component={LoginPage} />
      <Route path={appRoutes.homePage} exact component={HomePage} />
      <Route path={appRoutes.onboardingPage} exact component={OnboardingPage} />
      <Redirect path="*" to={appRoutes.LoginPage} />
    </Switch>
  );
};

const mapStateToProps = (state) => {
  console.log("APP STATE: ", state);
  return {
    isAuthenticated: state.isAuthenticated,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    retryLogin: () => dispatch(retryLogin()),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
