import React, { useState, useEffect } from "react";
import "./AppCard.scss";
import { Grid, Image, Card } from "semantic-ui-react";

const AppCard = React.memo((props) => {
  const [isSelected, setisSelected] = useState(false);

  const { alreadySelected } = props;

  useEffect(() => {
    if (alreadySelected) setisSelected(alreadySelected);
  }, [alreadySelected]);

  useEffect(() => {
    props.onClicked(isSelected, {
      app: props.data._id,
      url: props.data.url,
    });
  }, [isSelected]);

  const onCardClick = () => {
    setisSelected((prev) => !prev);
  };

  return (
    <Card
      className={isSelected ? "AppCard active" : "AppCard"}
      onClick={props.clickable && onCardClick}
    >
      <Card.Content className="AppCard-banner">
        <Image src={props.data.icon} className="AppCard-image"></Image>
      </Card.Content>
      <Card.Content className="AppCard-content">
        <Card.Header>
          <div className="AppCard-content-title">{props.data.name}</div>
        </Card.Header>
      </Card.Content>
    </Card>
  );
});

export default AppCard;
