import envsTest from "./environments.test";
import envsDev from "./environments.dev";
import envsProd from "./environments.prod";

const envs = () => {
  if (process.env.NODE_ENV === "production") return envsProd;
  else if (process.env.NODE_ENV === "development") return envsDev;
  else if (process.env.NODE_ENV === "test") return envsTest;
};

console.log(envs());

export default {
  headerName: "Dash Dash Go",
  serverHost: envs().serverHost,
};
