import React, { useState, useEffect } from "react";
import { Form, Transition, Button } from "semantic-ui-react";
import { withRouter } from "react-router-dom";
import { authRegister, updateUserProfile } from "../../../store/actions";
import { connect } from "react-redux";
import "./SignupForm.scss";
import appRoutes from "../../../configs/routes";

const SignupForm = React.memo((props) => {
  const [nameErr, setNameErr] = useState(null);
  const [emailErr, setEmailErr] = useState(null);
  const [passErr, setPassErr] = useState(null);
  const [companyErr, setCompanyErr] = useState(null);
  const [signupCreds, setSignupCreds] = useState({
    email: "",
    password: "",
    name: "",
    company: "",
  });

  const [isError, setisError] = useState(true);
  const [isSignupFirst, setIsSignupFirst] = useState(true);
  const [isSignupSecond, setIsSignupSecond] = useState(false);

  useEffect(() => {
    if (props.user && props.user.email) {
      setIsSignupFirst(false);
      setIsSignupSecond(true);
      props.showSecondStep();
    }
    if (props.user && props.user.organization) {
      setSignupCreds((prev) => {
        return {
          ...prev,
          company: props.user.organization,
        };
      });
    }
    if (props.user && props.user.name) {
      setSignupCreds((prev) => {
        return {
          ...prev,
          name: props.user.name,
        };
      });
    }
  }, [props.user]);

  useEffect(() => {
    if (props.isOnboarded) props.history.push(appRoutes.onboardingPage);
  }, [props.isOnboarded]);

  const resetErrs = () => {
    setNameErr(null);
    setEmailErr(null);
    setPassErr(null);
    setisError(false);
  };

  const validateEmail = () => {
    setEmailErr(null);
    setisError(false);
    const emailRegex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;

    if (!signupCreds.email) {
      setEmailErr({
        content: "Enter your email address",
        pointing: "below",
      });
      setisError(true);
    } else if (!emailRegex.test(signupCreds.email)) {
      setEmailErr({
        content: "Invalid email address",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validatePass = () => {
    setPassErr(null);
    setisError(false);
    if (!signupCreds.password) {
      setPassErr({
        content: "Enter your password",
        pointing: "below",
      });
      setisError(true);
    } else if (signupCreds.password.length < 8) {
      setPassErr({
        content: "Password must be greater than length 7",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const submitRegistration = (e) => {
    e.preventDefault();
    validateEmail();
    validatePass();

    if (!isError) {
      props.authRegister(signupCreds);
    }
  };

  const validateName = () => {
    setNameErr(null);
    setisError(false);
    if (!signupCreds.name) {
      setNameErr({
        content: "Enter your full name",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validateCompany = () => {
    setNameErr(null);
    setisError(false);
    if (!signupCreds.company) {
      setCompanyErr({
        content: "Enter your company name",
        pointing: "below",
      });
      setisError(true);
    }
  };

  const validateForm = (e) => {
    e.preventDefault();
    // props.completeSecondStep();
    resetErrs();
    validateName();
    validateCompany();

    if (!isError) {
      props.updateUserProfile(signupCreds);
    }
  };

  return (
    <Transition.Group animation="horizontal flip" duration="300">
      {isSignupFirst && (
        <Form className="signup-form" onSubmit={submitRegistration}>
          <React.Fragment>
            <Form.Field>
              <Form.Input
                fluid
                icon="envelope open outline"
                iconPosition="left"
                placeholder="Your email address"
                value={signupCreds.email}
                error={emailErr}
                onChange={(event) => {
                  const _email = event.target.value;
                  setSignupCreds((prev) => ({
                    email: _email,
                    password: prev.password,
                  }));
                  validateEmail();
                }}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                type="password"
                icon="eye slash outline"
                iconPosition="left"
                placeholder="Password"
                value={signupCreds.password}
                error={passErr}
                onChange={(event) => {
                  const _pass = event.target.value;
                  setSignupCreds((prev) => ({
                    ...prev,
                    email: prev.email,
                    password: _pass,
                  }));
                  validatePass();
                }}
              />
            </Form.Field>
            <Button
              fluid
              color="red"
              onClick={submitRegistration}
              loading={props.isLoading}
              disabled={isError}
            >
              REGISTER
            </Button>
          </React.Fragment>
        </Form>
      )}
      {isSignupSecond && (
        <Form className="signup-form" onSubmit={validateForm}>
          <React.Fragment>
            <Form.Field>
              <Form.Input
                fluid
                icon="user outline"
                iconPosition="left"
                placeholder="Full name"
                value={signupCreds.name}
                error={nameErr}
                onChange={(event) => {
                  const _name = event.target.value;
                  setSignupCreds((prev) => ({
                    ...prev,
                    name: _name,
                  }));
                  validateName();
                }}
              />
            </Form.Field>
            <Form.Field>
              <Form.Input
                fluid
                icon="building outline"
                iconPosition="left"
                placeholder="Company Name"
                value={signupCreds.company}
                error={companyErr}
                onChange={(event) => {
                  const _company = event.target.value;
                  setSignupCreds((prev) => ({
                    ...prev,
                    company: _company,
                  }));
                  validateCompany();
                }}
              />
            </Form.Field>
            <Form.Field>
              <Form.Button
                fluid
                iconPosition="left"
                color="green"
                loading={props.isLoading}
                disabled={isError}
                onClick={validateForm}
              >
                SUBMIT
              </Form.Button>
            </Form.Field>
          </React.Fragment>
        </Form>
      )}
    </Transition.Group>
  );
});

const mapStateToProps = (state) => {
  console.log(state);
  return {
    user: state.user,
    isOnboarded: state.isOnboarded,
    isLoading: state.isLoading,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    authRegister: (signupCreds) =>
      dispatch(
        authRegister({
          email: signupCreds.email,
          password: signupCreds.password,
        })
      ),
    updateUserProfile: (signupCreds) =>
      dispatch(
        updateUserProfile({
          name: signupCreds.name,
          organization: signupCreds.company,
        })
      ),
  };
};

export default withRouter(
  connect(mapStateToProps, mapDispatchToProps)(SignupForm)
);
